import React, { Component, useState } from 'react'
import QRCode from 'qrcode-react'
import QrcodeReader from 'react-qr-reader';


function phone() {

    var iDevices = [
        'iPad Simulator',
        'iPhone Simulator',
        'iPod Simulator',
        'iPad',
        'iPhone',
        'iPod',
        'Android'
    ];

    if (!!navigator.platform) {
        while (iDevices.length) {
            if (navigator.platform === iDevices.pop()) { return true; }
        }
    }

    return false;
}

export default function Qrcode() {
    let [value, setValue] = useState('');
    let [scanValue, setScanValue] = useState('');
    return (
        <div className="box box-primary">
            <div className="box-header with-border">
                <h3 className="box-title">QRCode</h3>
            </div>
            <div className="box-body" >
                <div className="form-group col-sm-12 col-md-6" >
                    <label>生成QRCode</label>
                    <input type="text"
                        className="form-control"
                        placeholder="QRCode Value"
                        onChange={(e) => { setValue(e.target.value) }}
                    />
                    <QRCode size={300} value={value} />
                </div>
                {!phone() &&
                    <div className="form-group col-sm-12 col-md-6" >
                        <label>讀取QRCode</label>
                        <input type="text"
                            className="form-control"
                            placeholder="Scan QRCode"
                            disabled
                            value={scanValue}
                        />
                        <div style={{ width: '300px' }}>
                            <QrcodeReader
                                onScan={(value) => {
                                    if (value) {
                                        setScanValue(value)
                                    }
                                }}
                                width={300} />
                        </div>
                    </div>
                }
            </div>
        </div>

    )
}